}
.slider {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.slider--back {
  left: 0;
}
.slider--front {
  right: 0;
}
.img-wrapper--back .opacity-wrapper {
  position: absolute;
